import m from "mithril";
import "polythene-css-classes";
import { styler } from "polythene-css";

const blockSize = 40;
const blockSizePx = blockSize + "px";

const styles = [{
  ".test-css-classes": {
    " .block": {
      "min-width": blockSizePx,
      height: blockSizePx,
      color: "#fff",
      "text-align": "center",
      "line-height": blockSizePx,

      "&:nth-child(1)": {
        background: "#311B92"
      },
      "&:nth-child(2)": {
        background: "#4527A0"
      },
      "&:nth-child(3)": {
        background: "#512DA8"
      },
      "&:nth-child(4)": {
        background: "#5E35B1"
      }
    }
  }
}];

styler.add("app-buttons", styles);

const blocks = [1, 2, 3, 4].map(num =>
  m(".block", num));

export const tests = [{
  name: "Should be stacked vertically",
  component: {
    view: () => m(".screen.layout.vertical",
      blocks
    )
  }
}];